<template>
	<div id="recharge">
		<common-header title="充值"></common-header>
		<div class="recharge__input">
			<input type="number" placeholder="充值金额(元)" v-model="amount">
		</div>
		<div class="recharge__type">
			<div class="type__title">选择充值方式</div>
			<div class="type__btn" @click="paySubmit">
				<div class="btn__info">
					<div class="btn__info__logo">
						<img src="../../../assets/wechat.png" alt="">
					</div>
					<div class="btn__info__mes">
						<div class="mes__title">微信支付</div>
						<div class="mes__tips">推荐使用</div>
					</div>
				</div>
			</div>
			<!--<div class="type__btn" v-if="!iswechat">
				<div class="btn__info">
					<div class="btn__info__logo">
						<img src="../../../assets/alipay.png" alt="">
					</div>
					<div class="btn__info__mes">
						<div class="mes__title">支付宝</div>
						<div class="mes__tips">支付宝支付,安全快速</div>
					</div>
				</div>
			</div>-->
		</div>
	</div>
</template>

<script type="text/javascript">
	export default {
		data() {
			return {
				amount: '',
				iswechat:false,
			}
		},
		created(){
			this.iswechat = this.$wechat.isWeiXin()
		},
		methods: {
			paySubmit() {
				if(this.amount > 0){
					let payment = {
						a: 1,
						amount: this.amount,
						payment: 11,
					}
					this.$Api.PaySubmit(payment).then((res) => {
						console.log(res)
						if(res.q.s == 0) {
							this.wxPayObj = JSON.parse( res.q.wxPay )
							this.onBridgeReady()
						}else {
							this.$toast(res.q.d)
						}
					})
				}else{
					this.$toast('请输入正确的金额')
				}
				
			},
			onBridgeReady() {
				let self = this
				console.log(this.wxPayObj)
				WeixinJSBridge.invoke(
					'getBrandWCPayRequest', {
						"appId": this.wxPayObj.appId, //公众号名称，由商户传入     
						"timeStamp": this.wxPayObj.timeStamp, //时间戳，自1970年以来的秒数     
						"nonceStr": this.wxPayObj.nonceStr, //随机串     
						"package": this.wxPayObj.package,
						"signType": this.wxPayObj.signType, //微信签名方式：     
						"paySign":  this.wxPayObj.paySign//微信签名 
					},
					function(res) {
						console.log(res)
						if(res.err_msg == "get_brand_wcpay_request:ok") {
//							self.$toast.replace({name:'PaySuccess'})
						} // 使用以上方式判断前端返回,微信团队郑重提示：res.err_msg将在用户支付成功后返回    ok，但并不保证它绝对可靠。
					}
				);
			},
		},
	}
</script>

<style lang="scss">
	#recharge {
		height: 100%;
		background: #f0f0f0;
		.recharge__input {
			padding: 0 0.4rem;
			width: 100%;
			height: 1.2rem;
			background: #fff;
			input {
				border: none;
				outline: none;
				height: 1.2rem;
				width: 100%;
				font-size: 0.4rem;
			}
		}
		.recharge__type {
			margin-top: 0.267rem;
			background: #fff;
			.type__title {
				padding: 0 0.4rem;
				height: 1.067rem;
				line-height: 1.067rem;
				font-size: 0.4rem;
				border-bottom: 1px solid #eee;
			}
			.type__btn {
				position: relative;
				padding: 0 0.4rem;
				box-sizing: content-box;
				.btn__info {
					display: flex;
					align-items: center;
					height: 1.867rem;
					.btn__info__logo {
						margin-right: 0.4rem;
						width: 0.5867rem;
						height: 0.5867rem;
						img {
							width: 0.5867rem;
							height: 0.5867rem;
							vertical-align: middle;
						}
					}
					.mes__title {
						padding-bottom: 0.1rem;
						font-size: 0.4rem;
						color: #333;
					}
					.mes__tips {
						font-size: 0.3733rem;
						color: #999;
					}
				}
				&:after {
					content: '';
					position: absolute;
					top: 50%;
					right: 0.4rem;
					width: 0.173rem;
					height: 0.307rem;
					transform: translateY(-50%);
					background-image: url('../../../assets/arrow_gray.png');
					background-size: 100%;
				}
			}
			.type__btn+.type__btn {
				border-top: 1px solid #eee;
			}
		}
	}
</style>


